                <v-divider></v-divider>
                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：jsonp的原理就是通过script标签发送get请求，script标签会将返回的数据当做js代码执行</p>
                    <pre class="code">{{pres[0]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
