                <v-card-text>
                    <p class="subtitle-1"><span class="font-weight-bold">说明</span>：因为服务端程序和服务端程序之间发送ajax不存在跨域问题，所以可以使用一个中介服务端程序，去访问正式的服务端程序</p>
