<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
        客户端的域名 http://localhost:5500
        服务端的域名 http://localhost:3000

        客户端的代码 
            function fn(data) {
                console.log(data);
            }
            \<script src="http://localhost:3000/jsonp?callback=fn"\>\<\/script\>

        服务端的代码
            app.get("/jsonp", function(req, res, next){
                const sendData = '我是服务器传送的数据';
                res.send(\`\${req.query.callback}('\${sendData}')\`);
            }) 
    `,
    `
        客户端的域名 http://localhost:5500
        服务端的域名 http://localhost:3000

        客户端代码
            fetch('http://localhost:3000/jsonp').then(res => {
                if(res.ok) {
                    return res.text();
                }
            }).then(res => {
                console.log(res);
            })

        服务端代码
            app.all("*",function(req,res,next){
                // 所有路由
                res.header("Access-Control-Allow-Origin", "http://localhost:5500");
                res.header("Access-Control-Allow-Headers", "content-type");
                res.header("Access-Control-Allow-Methods", "DELETE,PUT,POST,GET,OPTIONS");
                if (req.method.toLowerCase() == 'options') res.send(200);  // 请求预检
                else next();
            });

            app.get("/jsonp", function(req, res, next){
                const sendData = '我是服务器传送的数据';
                res.send(sendData);
            })
    `,
    `
        客户端的域名（任意）
        中介服务端程序的域名 http://localhost:3000
        正式服务端程序的域名 http://localhost:3001

        客户端代码
            fetch('/intermediary').then(res => {
                if(res.ok) {
                    return res.text();
                }
            }).then(res => {
                console.log(res);
            })
        中介服务端代码
            const express = require('express');
            const PORT = 3000;
            const app = express();
            const request = require('request');
            app.use(express.static('public'));
            app.all("*",function(req,res,next){
                // 所有路由
                res.header("Access-Control-Allow-Origin", "http://127.0.0.1:5500");
                res.header("Access-Control-Allow-Headers", "content-type");
                res.header("Access-Control-Allow-Methods", "DELETE,PUT,POST,GET,OPTIONS");
                if (req.method.toLowerCase() == 'options') res.send(200);  
                else next();
            });
            app.get("/intermediary", function(req, res, next){
                request('http://localhost:3001/test', (err, realRes, body) => {
                    if (!err && realRes.statusCode == 200) {
                        res.send(body); 
                    }
                })
            });
            app.listen(PORT, function() {
                console.log('Server listening on http://localhost:' + PORT);
            });
        正式服务端代码
            app.get("/test", function(req, res, next){
                res.end('我是正式服务器的数据');
            })
    `,
    `
        客户端的域名 http://localhost:3000
        服务端的域名 http://localhost:3001

        客户端代码
            <script src="https://cdnjs.cloudflare.com/ajax/libs/socket.io/2.2.0/socket.io.js"><\/script>
            var socket = io("http://localhost:3001");
            socket.on("receivefromserver", function(msg) {
                console.log(msg);
            });
            socket.emit('receivefromclient', '收到消息!');

        服务端代码
            const express = require('express');
            let PORT = 3001;
            const app = express();
            const io = require('socket.io')(app.listen(PORT, function() {
                console.log('Server listening on http://localhost:' + PORT);
            }));
            io.on('connection', (socket) => {
                console.log("Client connected!");
                socket.on('receivefromclient', (msg) => {
                    console.log(msg);
                })
                socket.emit('receivefromserver', '服务端的消息');
            });
    `,
    `
        主页面的域名 http://localhost:3000
        iframe页面的域名 http://localhost:3001

        主页面的代码
            html
                <iframe src="http://localhost:3001/index.html" frameborder="0"></iframe>
            js
                const ifr = document.querySelector('iframe');
                ifr.contentWindow.postMessage('我是主页面传递的数据', 'http://localhost:3001');

                window.addEventListener('message', (e) => {
                    console.log('主页面', e.data, e.origin);
                })

        iframe页面的代码
            js
                window.addEventListener('message', (e) => {
                    console.log('iframe', e.data, e.origin);
                    e.source.postMessage('iframe中的页面接受到数据了', e.origin);
                })
    `,
    `
        页面的代码全部扔到nginx服务器中，地址暂为 http://localhost:3000
        服务端所在的域名 http://localhost:3001

        配置nginx
            server {
                listen 3000;
                server_name localhost;
                location ^~ /api/ {
                    proxy_pass http://localhost:3001/;
                }
                location / {
                    root html/app;
                    index index.html index.htm;
                }
            }
    `
],
      time: "2019-10-14",
      title: "跨域的几种常用方式",
      items: [
