          target: "#proxy"
        },
        { divider: true },
        {
          title: "websocket",
          target: "#websocket"
        },
        { divider: true },
        {
          title: "postMessage",
          target: "#postMessage"
        },
        { divider: true },
        {
          title: "nginx",
          target: "#nginx"
        },
        { divider: true },
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

